export default {
  name:'audioPlay',
  data () {
      return {
          isClick:false,
          counts:{
              m:'00',
              s:'00'
          },
          num:{
              m:'00',
              s:'00'
          },
          maxTime:0,
          nowTime:0,
      }
  },
  computed: {
    
  },
  methods:{
      timer() {
          const that = this
          let second = that.counts.s
          let minute = that.counts.m
          second++
          if (second >= 60) {
              second = 0  //  60
              minute++
              // if (minute >= 60) {
              //     minute = 0  //  60
              // }
              if (minute < 10) {
              // 10
                  that.counts.m='0'+minute;
                  that.counts.s=second;
              } else {
                  that.counts.m=minute;
                  that.counts.s=second;
              }
          }
          if (second < 10) {
              // 10
              that.counts.m=minute;
              that.counts.s='0'+second;
          } else {
              that.counts.m=minute;
              that.counts.s=second;
          }
      },
      readPlay(){//
          const maxTime=parseInt(this.$refs.test.duration);
          this.maxTime=maxTime;
          this.num.m=parseInt(maxTime/60);
          this.num.s=maxTime%60;
          if(this.num.m<10){
              this.num.m='0'+this.num.m
          }
          if(this.num.s<10){
              this.num.s='0'+this.num.s
          }
          
      }
  },
  created(){
      
  },
  mounted(){

  }
}



